@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
 margin: 0;
 padding: 0;
 box-sizing: border-box;
}

div {
 width: 90%;
 height: 100vh;
 display: flex;
 flex-direction: column;
 flex-wrap: wrap;
 align-items: center;
 justify-content: flex-start;
 text-align: center;
 margin: 0 auto;
}

input{
 display: inline-block;
 height: 20px;
 width: 20px;
 cursor: pointer;
 margin-top: 20px; 
 }

input:focus{
 outline: 0;
}

label {
 display: inline-block;
 vertical-align: top;
 font-size: 30px;
}

button{
 font-size: 25px;
 background: none;
 border: 2px solid rgb(187, 90, 90);
 border-radius: 15px;
 padding: 5px 10px;
 margin-top: 20px;
 outline: none;
}

span{
 display: inline-block;
 height: 36px;
 width: 36px;
 background-image: url(data:image/png;base64,);
}

h1{
 font-family: 'Ubuntu', sans-serif;
 font-size: 43px;
 color:  rgb(216, 43, 43);
 margin-top: 20px;
}

p {
 margin-top: 80px;
 font-size: 28px;
}

.fajrant{
 font-size: 45px;
 color: red;
}
